import React from 'react'

import CustomBreadcrumb from '@common/CustomBreadcrumb'
import TypingCard from '@common/TypingCard'

class Dashboard extends React.Component {
  render() {
    const cardContent = `<ul class="card-ul">
            <li>react</li>
            <li>CRA, UI Antd Design, Yarn</li>
            <li>react + react router + react redux + redux thunk + less</li>
            <li>
              Echart, Fabric, Openlayer, Video,
               FortAwesome, Websocket
            </li>
        </ul>`
    return (
      <div>
        <CustomBreadcrumb />
        <TypingCard source={cardContent} id="howUse" title="" />
      </div>
    )
  }
}

export default Dashboard
