import React, { Component } from 'react'
import { Col, Row, Table, Spin, message } from 'antd'
import ReconnectingWebSocket from 'reconnectingwebsocket'

import CustomBreadcrumb from '@common/CustomBreadcrumb'
import TypingCard from '@common/TypingCard'
import AppHistory from '@common/AppHistory'
import styles from './index.module.less'

const { Column } = Table

class index extends Component {
  constructor(props) {
    super(props)
    this.state = {
      loading: true,
      users: []
    }
    this.ws = null
  }

  render() {
    const cardContent = `<ul class="card-ul">
            <li>WebsocketWebSocket API</li>
            <li>Websocket yarn add reconnectingwebsocket</li>
            <li><a href="https://www.npmjs.com/package/reconnectingwebsocket" target="#">reconnectingwebsocket npm</a></li>
            <li><a href="http://coolaf.com/tool/chattest" target="#">websocket</a></li>
        </ul>`
    const { users, loading } = this.state
    return (
      <div className={styles.websocketPage}>
        <CustomBreadcrumb arr={['Websocket Demo']} />
        <TypingCard source={cardContent} id="howUse" title="" />
        <Row gutter={20}>
          <Col span={24}>
            <Spin spinning={loading}>
              <Table
                rowKey="id"
                defaultExpandAllRows={true}
                bordered={true}
                className={styles.reacorListTable}
                dataSource={users}
                pagination={false}
                rowClassName={(record, index) =>
                  index % 2 === 0 ? 'active' : 'lop'
                }
              >
                <Column dataIndex="id" title="id" />
                <Column dataIndex="username" title="" />
                <Column dataIndex="sex" title="" />
                <Column dataIndex="age" title="" />
                <Column dataIndex="hobby" title="" />
              </Table>
            </Spin>
          </Col>
        </Row>
      </div>
    )
  }

  componentDidMount() {
    this.fetchData()
  }

  fetchData = () => {
    try {
      this.ws = new ReconnectingWebSocket(
        'ws://123.207.167.163:9010/ajaxchattest'
      )
    } catch (e) {
      message.error('websocket' + e)
      setTimeout(() => {
        AppHistory.goback()
      }, 2000)
    }
    // console.log(this.ws)
    this.ws.onopen = data => {
      console.log('Connection open ...')
      this.ws.send(
        `[{"id": 1,"username": "","sex": "","age": 20,"hobby": ""},{"id": 2,"username": "","sex": "","age": 32,"hobby": ""}]`
      )
    }
    this.ws.onmessage = data => {
      console.log(data.data)
      const users = JSON.parse(
        data.data.replace('<b></b>')
      )
      this.setState({ loading: false, users })
      // message.success(data.data)
      this.ws.close()
    }
  }

  componentWillUnmount() {
    if (!this.ws) {
      this.ws.close()
    }
  }
}

export default index
