import React, { Component } from 'react'
import { Card, Col, Row } from 'antd'
import echarts from 'echarts/lib/echarts'
import 'echarts/lib/chart/bar'
import 'echarts/lib/chart/line'
import 'echarts/lib/chart/pie'
import 'echarts/lib/chart/heatmap'
import 'echarts/lib/chart/scatter'
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/title'
import 'echarts/lib/component/visualMap'
import 'echarts/extension/bmap/bmap'

// import Axios from '@common/Axios'
import CustomBreadcrumb from '@common/CustomBreadcrumb'
import TypingCard from '@common/TypingCard'
import styles from './index.module.less'
import { geoData, hours, days, hotmap } from './data'

class index extends Component {
  render() {
    const cardContent = `<ul class="card-ul">
            <li>ECharts JavaScript  PC IE8/9/10/11ChromeFirefoxSafari ZRender</li>
            <li>Echarts yarn add echarts</li>
            <li>echarts bmaphtml,
            import 'echarts/lib/component/visualMap'
            import 'echarts/extension/bmap/bmap'</li>
            <li><a href="https://echarts.baidu.com/" target="#">echarts</a></li>
        </ul>`
    return (
      <div className={styles.echartsPage}>
        <CustomBreadcrumb arr={['Echarts Demo']} />
        <TypingCard source={cardContent} id="howUse" title="" />
        <Row gutter={20}>
          <Col span={12}>
            <Card
              hoverable
              bordered={false}
              title=""
              style={{ minHeight: 400 }}
              id="1"
            >
              <div id="column" style={{ width: '95%', height: 400 }} />
            </Card>
          </Col>
          <Col span={12}>
            <Card
              hoverable
              bordered={false}
              title=""
              style={{ minHeight: 400 }}
              id="4"
            >
              <div id="echarts4" style={{ width: '90%', height: 400 }} />
            </Card>
          </Col>
        </Row>
        <Row gutter={20}>
          <Col span={12}>
            <Card
              hoverable
              bordered={false}
              title=""
              style={{ minHeight: 400 }}
              id="2"
            >
              <div id="line" style={{ width: '95%', height: 400 }} />
            </Card>
          </Col>
          <Col span={12}>
            <Card
              hoverable
              bordered={false}
              title=""
              style={{ minHeight: 400 }}
              id="5"
            >
              <div id="echarts5" style={{ width: '90%', height: 400 }} />
            </Card>
          </Col>
        </Row>
        <Row gutter={20}>
          <Col span={12}>
            <Card
              hoverable
              bordered={false}
              title=""
              style={{ minHeight: 400 }}
              id="3"
            >
              <div id="echarts3" style={{ width: '95%', height: 400 }} />
            </Card>
          </Col>
          <Col span={12}>
            <Card
              hoverable
              bordered={false}
              title=""
              style={{ minHeight: 400 }}
              id="6"
            >
              <div id="echarts6" style={{ width: '95%', height: 400 }} />
            </Card>
          </Col>
        </Row>
        <Row gutter={20}>
          <Col span={12}>
            <Card
              hoverable
              bordered={false}
              title=""
              style={{ minHeight: 400 }}
              id="7"
            >
              <div id="echarts7" style={{ width: '95%', height: 400 }} />
            </Card>
          </Col>
          <Col span={12}>
            <Card
              hoverable
              bordered={false}
              title=""
              style={{ minHeight: 400 }}
              id="8"
            >
              <div id="echarts8" style={{ width: '95%', height: 400 }} />
            </Card>
          </Col>
        </Row>
      </div>
    )
  }

  componentDidMount() {
    this.initColumnEcharts()
    this.initLineEcharts()
    this.initScatter()
    this.initStack()
    this.initStackLine()
    this.initPieCharts()
    this.initHeatMap()
    this.initHeatMap2()
  }

  initColumnEcharts = () => {
    const myChart = echarts.init(document.getElementById('column'))
    myChart.setOption({
      title: {
        text: 'ECharts '
      },
      tooltip: {},
      xAxis: {
        data: ['', '', '', '', '', '']
      },
      yAxis: {},
      series: [
        {
          name: '',
          type: 'bar',
          data: [5, 20, 36, 10, 10, 20]
        }
      ]
    })
    myChart.resize()
  }

  initLineEcharts = () => {
    const myChart = echarts.init(document.getElementById('line'))
    myChart.setOption({
      title: {
        text: 'ECharts '
      },
      xAxis: {
        type: 'category',
        data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
      },
      tooltip: {
        trigger: 'axis'
      },
      yAxis: {
        type: 'value'
      },
      series: [
        {
          data: [820, 932, 901, 934, 1290, 1330, 1320],
          type: 'line',
          name: ''
        }
      ]
    })
    myChart.resize()
  }

  initScatter = () => {
    const myChart = echarts.init(document.getElementById('echarts3'))
    const option = {
      title: {
        text: 'ECharts '
      },
      xAxis: {},
      yAxis: {},
      tooltip: {
        formatter: function(param) {
          var value = param.value
          return value[0] + '(' + value[1] + ')'
        }
      },
      series: [
        {
          symbolSize: 20,
          data: [
            [10.0, 8.04],
            [8.0, 6.95],
            [13.0, 7.58],
            [9.0, 8.81],
            [11.0, 8.33],
            [14.0, 9.96],
            [6.0, 7.24],
            [4.0, 4.26],
            [12.0, 10.84],
            [7.0, 4.82],
            [5.0, 5.68]
          ],
          type: 'scatter'
        }
      ]
    }
    myChart.setOption(option)
    // myChart.resize()
  }

  initStack = () => {
    const myChart = echarts.init(document.getElementById('echarts4'))
    const option = {
      title: {
        text: 'ECharts '
      },
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          // 
          type: 'shadow' // 'line' | 'shadow'
        }
      },
      // legend: {
      //   data: ['', '', '', '', '']
      // },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
      },
      xAxis: {
        type: 'value'
      },
      yAxis: {
        type: 'category',
        data: ['', '', '', '', '', '', '']
      },
      series: [
        {
          name: '',
          type: 'bar',
          stack: '',
          label: {
            normal: {
              show: true,
              position: 'insideRight'
            }
          },
          data: [320, 302, 301, 334, 390, 330, 320]
        },
        {
          name: '',
          type: 'bar',
          stack: '',
          label: {
            normal: {
              show: true,
              position: 'insideRight'
            }
          },
          data: [120, 132, 101, 134, 90, 230, 210]
        },
        {
          name: '',
          type: 'bar',
          stack: '',
          label: {
            normal: {
              show: true,
              position: 'insideRight'
            }
          },
          data: [220, 182, 191, 234, 290, 330, 310]
        },
        {
          name: '',
          type: 'bar',
          stack: '',
          label: {
            normal: {
              show: true,
              position: 'insideRight'
            }
          },
          data: [150, 212, 201, 154, 190, 330, 410]
        },
        {
          name: '',
          type: 'bar',
          stack: '',
          label: {
            normal: {
              show: true,
              position: 'insideRight'
            }
          },
          data: [820, 832, 901, 934, 1290, 1330, 1320]
        }
      ]
    }
    myChart.setOption(option)
    myChart.resize()
  }

  initStackLine = () => {
    const myChart = echarts.init(document.getElementById('echarts5'))
    const option = {
      title: {
        text: 'ECharts '
      },
      tooltip: {
        trigger: 'axis'
      },
      // legend: {
      //   data: ['', '', '', '', '']
      // },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
      },
      // toolbox: {
      //   feature: {
      //     saveAsImage: {}
      //   }
      // },
      xAxis: {
        type: 'category',
        boundaryGap: false,
        data: ['', '', '', '', '', '', '']
      },
      yAxis: {
        type: 'value'
      },
      series: [
        {
          name: '',
          type: 'line',
          stack: '',
          data: [120, 132, 101, 134, 90, 230, 210]
        },
        {
          name: '',
          type: 'line',
          stack: '',
          data: [220, 182, 191, 234, 290, 330, 310]
        },
        {
          name: '',
          type: 'line',
          stack: '',
          data: [150, 232, 201, 154, 190, 330, 410]
        },
        {
          name: '',
          type: 'line',
          stack: '',
          data: [320, 332, 301, 334, 390, 330, 320]
        },
        {
          name: '',
          type: 'line',
          stack: '',
          data: [820, 932, 901, 934, 1290, 1330, 1320]
        }
      ]
    }
    myChart.setOption(option)
    myChart.resize()
  }

  initPieCharts = () => {
    const myChart = echarts.init(document.getElementById('echarts6'))
    const option = {
      // backgroundColor: '#2c343c',
      title: {
        text: 'ECharts '
      },
      tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b} : {c} ({d}%)'
      },
      series: [
        {
          name: '',
          type: 'pie',
          radius: '55%',
          center: ['50%', '50%'],
          data: [
            { value: 335, name: '' },
            { value: 310, name: '' },
            { value: 274, name: '' },
            { value: 235, name: '' },
            { value: 400, name: '' }
          ].sort(function(a, b) {
            return a.value - b.value
          }),
          roseType: 'radius',
          label: {
            normal: {
              textStyle: {
                color: 'rgba(255, 255, 255, 0.3)'
              }
            }
          },
          labelLine: {
            normal: {
              lineStyle: {
                color: 'rgba(255, 255, 255, 0.3)'
              },
              smooth: 0.2,
              length: 10,
              length2: 20
            }
          }
        }
      ]
    }
    myChart.setOption(option)
    myChart.resize()
  }

  initHeatMap = () => {
    const myChart = echarts.init(document.getElementById('echarts7'))
    const data = geoData.map(function(item) {
      return [item[1], item[0], item[2] || '-']
    })

    const option = {
      title: {
        text: 'ECharts '
      },
      tooltip: {
        position: 'top'
      },
      animation: false,
      // grid: {
      //   height: '70%',
      //   y: '1%'
      // },
      xAxis: {
        type: 'category',
        data: hours,
        splitArea: {
          show: true
        }
      },
      yAxis: {
        type: 'category',
        data: days,
        splitArea: {
          show: true
        }
      },
      visualMap: {
        min: 0,
        max: 10,
        calculable: true,
        orient: 'horizontal',
        left: 'center',
        bottom: '15%'
      },
      series: [
        {
          name: 'Punch Card',
          type: 'heatmap',
          data: data,
          label: {
            normal: {
              show: true
            }
          },
          itemStyle: {
            emphasis: {
              shadowBlur: 10,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            }
          }
        }
      ]
    }
    myChart.setOption(option)
  }

  initHeatMap2 = async () => {
    const myChart = echarts.init(document.getElementById('echarts8'))
    // const res = await Axios.get('/api/heatmap.json')
    // const data = res.data
    const data = hotmap
    var points = [].concat.apply(
      [],
      data.map(function(track) {
        return track.map(function(seg) {
          return seg.coord.concat([1])
        })
      })
    )
    myChart.setOption({
      animation: false,
      bmap: {
        center: [120.13066322374, 30.240018034923],
        zoom: 14,
        roam: true
      },
      visualMap: {
        show: false,
        top: 'top',
        min: 0,
        max: 5,
        seriesIndex: 0,
        calculable: true,
        inRange: {
          color: ['blue', 'blue', 'green', 'yellow', 'red']
        }
      },
      series: [
        {
          type: 'heatmap',
          coordinateSystem: 'bmap',
          data: points,
          pointSize: 5,
          blurSize: 6
        }
      ]
    })
  }
}

export default index
