import React, { Component } from 'react'
import { Card, Col, Row } from 'antd'
import 'video.js/dist/video-js.css'

import CustomBreadcrumb from '@common/CustomBreadcrumb'
import TypingCard from '@common/TypingCard'
import VideoPlayer from './VideoPlayer'

class index extends Component {
  render() {
    const cardContent = `<ul class="card-ul">
            <li>video.jsHTML5HTML5FlashYouTubeVimeoVideo.jshtml5mp4webmogv</li>
            <li>Video yarn add video.js</li>
            <li><a href="https://videojs.com/" target="#">Video</a> ,
            <a href="https://docs.videojs.com/tutorial-react.html" target="#">Video</a></li>
        </ul>`
    const videoJsOptions = {
      style: { height: '400px', width: '100%' },
      autoplay: true,
      controls: true,
      sources: [
        {
          src:
            '//s3.pstatp.com/aweme/resource/web/static/image/index/tvc-v2_30097df.mp4',
          type: 'video/mp4'
        }
      ]
    }
    return (
      <div>
        <CustomBreadcrumb arr={['Video Demo']} />
        <TypingCard source={cardContent} id="howUse" title="" />
        <Row gutter={20}>
          <Col span={24}>
            <Card
              hoverable
              bordered={false}
              title=""
              style={{ minHeight: 400 }}
              id="1"
            >
              <VideoPlayer {...videoJsOptions} />
            </Card>
          </Col>
        </Row>
      </div>
    )
  }
}

export default index
